<template>
	<div class="header _def-show ">
		<div class="container header__container">
			<div class="header__logo js-logo">
				<template v-if="!isIndexPage">
					<router-link to="/">
						<span class="logo">
							<img class="logo__img" :src="logo" alt="logo" height="66"
									width="212">
						</span>
					</router-link>
				</template>
				<template v-else>
					<span class="logo">
						<img class="logo__img" :src="logo" alt="logo" height="66"
								width="212">
					</span>
				</template>

			</div>
			<nav class="header__nav">
				<ul class="menu">
					<li class="menu__item" v-for="menuItem in headerMenu" :key="menuItem.id">
						<router-link class="menu__link" :to="menuItem.url" active-class="active">
							<span class="menu__text">{{menuItem.name}}</span>
						</router-link>
					</li>
				</ul>
			</nav>

			<div class="callback header__callback">
				<a :href="'tel:' + fomattedPhone" class="callback__link text--size-xl">
					{{decoratedPhone(fomattedPhone)}}
				</a>

				<button class="button js-init callback__btn" data-mfp="ajax" data-mfp-src="hidden/callback.php" data-param='{"key":"value"}'><span>Обратный звонок</span></button>
			</div>
		</div>
	</div>

</template>

<script>
	import { mapState } from 'vuex';
	export default {
		data() {
			return {
				phone: '+38 (067) 233-68-75',
				headerMenu: [
					{
						name: "О нас",
						url: "/about"
					},{
						name: "Каталог объектов",
						url: "/catalog"
					},{
						name: "Контакты",
						url: "/contacts"
					}
				],
			}
		},
		methods: {
			decoratedPhone (number) {
				return number.slice(0, 3) + ' (' + number.slice(3, 6) + ') ' + number.slice(6, 9) + '-' + number.slice(9, 11) + '-' + number.slice(11);
			}
		},
		computed: {
			isIndexPage() {
				return this.$route.name === 'home';
			},

			fomattedPhone () {
				return this.phone.replace(/[-()\s]/g, '');
			},

			...mapState([
				'logo'
			])
		},
		watch: {

		}
	}
</script>

<style lang="scss">
	@import '../sass/_blocks/header';
</style>